import "../styles/index.scss";
var filters = document.querySelectorAll(".collection__filter-item");
var collections = document.querySelectorAll(".collection__list");
filters.forEach(function (btn) {
    btn.addEventListener("mouseover", function () {
        var origin = btn.dataset.origin;
        switchCollection(origin);
        changeFilter(btn);
    });
});
var switchCollection = function (origin) {
    collections.forEach((function (collection) {
        collection.id == origin ? collection.classList.add("active") : collection.classList.remove("active");
    }));
};
var changeFilter = function (btn) {
    filters.forEach(function (filter) {
        filter == btn ? filter.classList.add("active") : filter.classList.remove("active");
    });
};
